
















































































































































import Vue from 'vue';
import UnitPlanOa from '@/models/UnitPlanActivityOa';
import UnitPlanActivity from '@/models/UnitPlanActivityOa';
import UnitPlanActivityOa from '@/models/UnitPlanActivityOa';
import UnitPlanActivityFile from '@/models/UnitPlanActivityFile';
import UnitPlanActivityDua from '@/models/UnitPlanActivityDua';

function requestDefaultValues () {
    return {
        selectedOAs: [],
        activityLer: '',
        files: [],
        selectedDuas: [],
        activityEva: {
            type: null,
            instrument: null,
            text: '',
        }
    }
}

export default Vue.extend({
    name: 'RomaActivityModalForm',
    props: {
        value: Boolean,
        activity: Object,
        maxWidth: {
            type: String,
            default: '1024',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
        unit: {
            type: Object,
            required: true,
        },
        selected: {
            type: Array
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            duas: [],
            evaluationTypes: [],
            instrumentTypes: [],
            request: requestDefaultValues(),
        };
    },
    computed: {
        UnitplanOAs () {
            const details = UnitPlanOa.query()
                .where('unitplan_id', this.unit.id)
                .with('oa')
                .get();

            return details.map(detail => detail.oa);
        },
        activityRules (value) {
            return [
                value => value.length >= 5 || 'La actividad no puede ser menor a 5 caracteres',
                value => value.length <= 100 || 'La actividad no puede ser mayor a 100 caracteres',
            ]
        }
    },
    watch: {
        activity (value) {
            if (value) {
                this.request.activityLer = value.name;
                this.request.selectedOAs = value.oas;
                this.request.selectedDuas = value.duas;
                this.request.files = value.files;
            } else {
                this.request = requestDefaultValues();
            }
        },
        selected (value) {
            if (value.length > 0) {
                this.request.selectedOAs = [];
                value.forEach(v => {
                    this.UnitplanOAs.forEach(o => {
                        if (v.id === o.id) this.request.selectedOAs.push(o);
                    })
                })
            }
        }
    },
    methods: {
        show (value: Boolean) {
            this.$emit('input', value);
        },
        save () {
            let data = {unitplan_id: this.unit.id, name: this.request.activityLer};
            if (this.activity) data.id = this.activity.id; // Si va a editar

            UnitPlanActivity.insertOrUpdate({data: data}).then(response => { // Si ya se inserto la actividad
                // TODO: OAs se repiten al editar actividad
                // OAs
                this.request.selectedOAs.forEach(oa => {
                    UnitPlanActivityOa.insertOrUpdate({
                        data: {
                            activity_id: response.unitplan_activities[0].id,
                            oa_id: oa.id,
                        }
                    })
                });
                // Files
                this.request.files.forEach(file => {
                    UnitPlanActivityFile.insertOrUpdate({
                        data: {
                            activity_id: response.unitplan_activities[0].id,
                        }
                    })
                })
                // Duas
                this.request.selectedDuas.forEach(dua => {
                    UnitPlanActivityDua.insertOrUpdate({
                        data: {
                            activity_id: response.unitplan_activities[0].id,
                            dua_id: dua.id,
                        }
                    })
                })

                this.$emit('activity-added');
                this.request = requestDefaultValues();
                this.show(false);
            })
        }
    },
});
